import React from 'react'
import './Projects.css'

const Project = (props) => {
    const contents = props.screenShots.map(
        (url, urlIndex)=>{
            return (
                <div className="Portrait" key={urlIndex}><img className="GalleryImage" src={url} alt={props.name}/></div>
            )
        }
    )

    return (
        <div className="Project">
            <button className='ProjectName' onClick={(event)=>props.clicked(event)}>{props.name}</button>
            <div className='ProjectContent'>
                <p>{props.description}</p>
                {contents}
            </div>
        </div>
    )
}

export default Project;