import React from 'react'
import Container from './Container.js';

const Footer = (props) =>{
    const style={
        width: '100%',
        textAlign: 'center',
        fontSize: '10px',
    }
    return(
        <div style={style}>
            <Container>
                <p>&copy;2019 Lin Chen</p>
            </Container>
        </div>
    )
}

export default Footer;